import React from 'react';

class Clock extends React.Component {

  constructor(props) {
    super(props);
    const fixedDate = (new Date()).setHours(8, 0, 0);
    const diff = new Date() - fixedDate;
    this.launchClock()
    this.state = {
      currentTime: (new Date()).toLocaleString(),
      mtime: diff,
      mcount: 0
    }
  }

  launchClock() {
    console.log(this.fixedDate)
    setInterval( () => {
      this.setState({
        currentTime: (new Date()).toLocaleString(),
        //mcount: this.state.mcount += 0.463
        mtime: new Date() - (new Date()).setHours(8, 0, 0),
        mcount: (this.state.mtime * (27.8 / (60 * 60 * 1000))).toFixed(4)
      })
    }, 1000)
  }

  render() {
    return (
      <div>
        
        <div>{this.state.currentTime}</div>
        <div>{this.state.mcount}</div>
      </div>)
  }

}

export default (Clock)